import React from 'react';
import PT from 'prop-types';
import { Link } from 'gatsby';
import Layout from '../../components/layout';
import SEO from '../../components/seo';
import styles from './index.module.scss';
import { CardBox } from '../../components/cardbox';


const CalculatorItem = ({ children, description, to }) => (
  <div className={styles.calculator_item}>
    <Link to={to} className={styles.fw}>
      <div>
        <div>
          <h3>
            {children}
          </h3>
          <hr />
        </div>
        {
          description
          && (
            <div>
              <p>{description}</p>
            </div>
          )
        }
      </div>
    </Link>
  </div>
);

CalculatorItem.propTypes = {
  children: PT.oneOfType([
    PT.arrayOf(PT.node),
    PT.node,
  ]).isRequired,
  description: PT.string.isRequired,
  to: PT.string.isRequired,
};

const Calculator = ({ location }) => (
  <Layout location={location} title="Calculators" withFooter={false}>
    <SEO
      title="Calculators"
      description="Useful apps and calculators. Word counter, money balance prediction, money inflation."
    />
    <CardBox>
      <div className={styles.calculator_grid}>
        <CalculatorItem
          to="/calculators/money_prediction"
          description="Do you know how much money will you have in the future? Let's look on that and compute that."
        >
          Future Money
        </CalculatorItem>
        <CalculatorItem
          to="/calculators/count_words"
          description="Easy words counter. Copy paste your text, you will see how many words are there."
        >
          Words Counter
        </CalculatorItem>
        <CalculatorItem
          to="/calculators/inflation"
          description="This calculator computes how the inflation impacts the value of your money."
        >
          Money Inflation
        </CalculatorItem>
      </div>
    </CardBox>
  </Layout>
);

Calculator.propTypes = {
  location: PT.shape({}).isRequired,
};


export default Calculator;
